import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Container,
  Grid,
  Typography,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getQuestions } from "../../redux/reducers/site";

const Faq = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { questions } = useSelector(({ site }) => site);

  useEffect(() => {
    dispatch(getQuestions());
  }, []);

  const [expanded, setExpanded] = useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <section className={classes.section}>
      <Container
        maxWidth="xl"
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <Typography variant="h2">
          Часто задаваемые{" "}
          <span
            className="yellow"
            style={{
              fontWeight: "bold",
            }}
          >
            вопросы
          </span>
        </Typography>
        <Grid
          container
          spacing={4}
          sx={{
            mt: { xs: 0, md: 5 },
          }}
        >
          <Grid item md={6}>
            {questions?.slice(0, 4)?.map((item, idx) => (
              <Accordion
                className={classes.accordion}
                key={idx}
                expanded={expanded === item.id}
                onChange={handleChange(item.id)}
              >
                <AccordionSummary
                  expandIcon={
                    <svg
                      width="37"
                      height="37"
                      viewBox="0 0 37 37"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <circle cx="18.5" cy="18.5" r="18.5" fill="#FFB951" />
                      <path
                        d="M18.364 20.9498L23.314 15.9998L24.728 17.4138L18.364 23.7778L12 17.4138L13.414 15.9998L18.364 20.9498Z"
                        fill="black"
                      />
                    </svg>
                  }
                >
                  <Typography variant="body1" sx={{ fontWeight: 500 }}>
                    {item.title}
                  </Typography>
                </AccordionSummary>
                <AccordionDetails sx={{ borderTop: "1px solid #DDDDDD" }}>
                  <Typography
                    variant="body2"
                    sx={{ fontWeight: 400, lineHeight: "150%!important" }}
                  >
                    {item.text}
                  </Typography>
                </AccordionDetails>
              </Accordion>
            ))}
          </Grid>
          <Grid
            item
            md={6}
            sx={{ pt: { xs: "0!important", lg: "32px!important" } }}
          >
            {questions?.slice(4)?.map((item, idx) => (
              <Accordion
                className={classes.accordion}
                key={idx}
                expanded={expanded === item.id}
                onChange={handleChange(item.id)}
              >
                <AccordionSummary
                  expandIcon={
                    <svg
                      width="37"
                      height="37"
                      viewBox="0 0 37 37"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <circle cx="18.5" cy="18.5" r="18.5" fill="#FFB951" />
                      <path
                        d="M18.364 20.9498L23.314 15.9998L24.728 17.4138L18.364 23.7778L12 17.4138L13.414 15.9998L18.364 20.9498Z"
                        fill="black"
                      />
                    </svg>
                  }
                >
                  <Typography variant="body1" sx={{ fontWeight: 500 }}>
                    {item.title}
                  </Typography>
                </AccordionSummary>
                <AccordionDetails sx={{ borderTop: "1px solid #DDDDDD" }}>
                  <Typography
                    variant="body2"
                    sx={{ fontWeight: 400, lineHeight: "150%!important" }}
                  >
                    {item.text}
                  </Typography>
                </AccordionDetails>
              </Accordion>
            ))}
          </Grid>
        </Grid>
      </Container>
    </section>
  );
};

export default Faq;

const useStyles = makeStyles((theme) => ({
  section: {
    padding: "60px 0 80px",
  },
  accordion: {
    borderRadius: "8px!important",
    boxShadow: "none!important",
    border: "none!important",
    background: "#F5F6FA!important",
    marginBottom: 16,
    padding: "27px 25px",

    [theme.breakpoints.down("md")]: {
      padding: "27px 16px 16px",
    },

    "&::before": {
      display: "none",
    },
  },
}));
